import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import styled from 'styled-components';
import { Activity } from './types';
import NotificationSystem from './components/NotificationSystem';

// Importaremos los componentes cuando los creemos
const Home = React.lazy(() => import('./components/Home'));
const CreatePoll = React.lazy(() => import('./components/CreatePoll'));
const RevealPoll = React.lazy(() => import('./components/RevealPoll'));

const AppContainer = styled.div`
  font-family: 'Press Start 2P', sans-serif;
  max-width: 100%;
  min-height: 100vh;
  background-color: #1a1a2e;
  color: #ffffff;
  padding: 20px;
  box-sizing: border-box;
  
  @media (min-width: 768px) {
    padding: 40px;
  }
`;

const LoadingScreen = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 24px;
  color: #ffd700;
`;

function App() {
  const [activities, setActivities] = useState<Activity[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [creatorId, setCreatorId] = useState<string>('');

  useEffect(() => {
    // Cargar las actividades desde el JSON
    fetch('/activities.json')
      .then(response => response.json())
      .then(data => {
        setActivities(data.activities);
        setLoading(false);
      })
      .catch(error => {
        console.error('Error loading activities:', error);
        setLoading(false);
      });
      
    // Generar o recuperar ID de creador
    const storedId = localStorage.getItem('creatorId');
    if (storedId) {
      setCreatorId(storedId);
    } else {
      const newId = Math.random().toString(36).substring(2, 15);
      localStorage.setItem('creatorId', newId);
      setCreatorId(newId);
    }
  }, []);

  if (loading) {
    return (
      <AppContainer>
        <LoadingScreen>Cargando actividades...</LoadingScreen>
      </AppContainer>
    );
  }

  return (
    <Router>
      <AppContainer>
        <React.Suspense fallback={<LoadingScreen>Cargando...</LoadingScreen>}>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/create" element={<CreatePoll activities={activities} />} />
            <Route path="/reveal/:encodedData" element={<RevealPoll activities={activities} />} />
          </Routes>
        </React.Suspense>
        
        {/* Sistema de notificaciones */}
        <NotificationSystem creatorId={creatorId} />
      </AppContainer>
    </Router>
  );
}

export default App;
