import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const NotificationContainer = styled.div`
  position: fixed;
  top: 20px;
  right: 20px;
  background-color: #2ecc71;
  color: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  max-width: 300px;
  animation: slideIn 0.5s ease-out;
  
  @keyframes slideIn {
    from {
      transform: translateX(100%);
      opacity: 0;
    }
    to {
      transform: translateX(0);
      opacity: 1;
    }
  }
`;

const NotificationTitle = styled.h3`
  margin: 0 0 10px 0;
  font-size: 1rem;
`;

const NotificationText = styled.p`
  margin: 0;
  font-size: 0.9rem;
`;

const NotificationButton = styled.button`
  background-color: white;
  color: #2ecc71;
  border: none;
  border-radius: 5px;
  padding: 8px 12px;
  margin-top: 10px;
  cursor: pointer;
  font-weight: bold;
  
  &:hover {
    background-color: #f8f9fa;
  }
`;

const NotificationBadge = styled(Link)`
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #e74c3c;
  color: white;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  font-size: 1.5rem;
  font-weight: bold;
  z-index: 1000;
  
  &:hover {
    transform: scale(1.1);
  }
`;

interface NotificationProps {
  creatorId: string;
}

const NotificationSystem: React.FC<NotificationProps> = ({ creatorId }) => {
  const [notification, setNotification] = useState<any>(null);
  const [showNotification, setShowNotification] = useState<boolean>(false);
  const [hasUnread, setHasUnread] = useState<boolean>(false);
  
  // Comprobar notificaciones cada 5 segundos
  useEffect(() => {
    const checkNotifications = () => {
      const notifications = JSON.parse(localStorage.getItem('notifications') || '{}');
      if (notifications[creatorId] && !showNotification) {
        setNotification(notifications[creatorId]);
        setShowNotification(true);
        setHasUnread(true);
      }
    };
    
    // Comprobar inmediatamente
    checkNotifications();
    
    // Configurar intervalo
    const interval = setInterval(checkNotifications, 5000);
    
    return () => clearInterval(interval);
  }, [creatorId, showNotification]);
  
  const handleDismiss = () => {
    setShowNotification(false);
    
    // Esperar un tiempo antes de permitir nuevas notificaciones
    setTimeout(() => {
      const notifications = JSON.parse(localStorage.getItem('notifications') || '{}');
      delete notifications[creatorId];
      localStorage.setItem('notifications', JSON.stringify(notifications));
    }, 500);
  };
  
  const handleViewDetails = () => {
    setShowNotification(false);
    setHasUnread(false);
  };
  
  return (
    <>
      {showNotification && notification && (
        <NotificationContainer>
          <NotificationTitle>Actividad seleccionada!</NotificationTitle>
          <NotificationText>
            Tu pareja ha seleccionado la actividad: <strong>{notification.activityName}</strong>
          </NotificationText>
          <div style={{ display: 'flex', gap: '10px', marginTop: '10px' }}>
            <NotificationButton onClick={handleDismiss}>Cerrar</NotificationButton>
            <NotificationButton onClick={handleViewDetails}>Ver detalles</NotificationButton>
          </div>
        </NotificationContainer>
      )}
      
      {hasUnread && !showNotification && (
        <NotificationBadge to="/" onClick={handleViewDetails}>
          1
        </NotificationBadge>
      )}
    </>
  );
};

export default NotificationSystem;
